import React from 'react';
import SearchIcon from '@material-ui/icons/Search';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import YouTubeIcon from '@material-ui/icons/YouTube';
import MicIcon from '@material-ui/icons/Mic';
import VideocamIcon from '@material-ui/icons/Videocam';
import DialpadOutlinedIcon from '@material-ui/icons/DialpadOutlined';
import NotificationsNoneOutlinedIcon from '@material-ui/icons/NotificationsNoneOutlined';
import "./Header.css";
import MenuOutlinedIcon from '@material-ui/icons/MenuOutlined';
const Header=()=>{
  return(
    <div className="header">
{/* three bar  */}
<div className="header__containerLogo">
<MenuOutlinedIcon className="header__threeBar"/>

{/* youtube logo */}
<YouTubeIcon className="header__logo"/>

<h2>Youtube</h2>

</div>
{/* search  */}
<div className="header__search">
  <input type="text"
  className="header__input"
  placeholder="Search"/>
{/* search icon */}
<SearchIcon className="header__searchIcon"/>
<MicIcon/>
  </div>
{/* mic */}

<div className="header__right">
{/* video */}
<VideocamIcon className="header__icon" />
{/* 6 box square */}
<DialpadOutlinedIcon className="header__icon"/>
{/* bell */}
<NotificationsNoneOutlinedIcon className="header__icon"/>
{/* profile pic  */}
{/* <img src="" alt="" className="header__profilePic"/> */}
   </div>
    </div>  
  )
}

export default Header;