import React from 'react';
import "./Sidebar.css";
import SidebarOptions from './SidebarOptions'
import HomeIcon from '@material-ui/icons/Home';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';
import AccessAlarmsIcon from '@material-ui/icons/AccessAlarms';
import MovieIcon from '@material-ui/icons/Movie';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';


const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
}));



const Sidebar = ()=>{
  return(
    <div className="sidebar">
      {/* options */}
      <SidebarOptions title="Home" Icon={HomeIcon}/>
      <SidebarOptions title="Trending" Icon={WhatshotIcon}/>
      <SidebarOptions title="Subscription" Icon={SubscriptionsIcon}/>
      <hr/>
      <SidebarOptions title="Library" Icon={VideoLibraryIcon}/>
      <SidebarOptions title="History" Icon={MovieIcon}/>   
      <SidebarOptions title="Your Videos" Icon={MovieIcon}/>
      <SidebarOptions title="Watch Later" Icon={AccessAlarmsIcon}/>


      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography>Accordion 2</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <div className="sidebar__list">
        <SidebarOptions title="Library" Icon={VideoLibraryIcon}/>
      <SidebarOptions title="History" Icon={MovieIcon}/>   
      <SidebarOptions title="Your Videos" Icon={MovieIcon}/>
      <SidebarOptions title="Watch Later" Icon={AccessAlarmsIcon}/>
         </div>
          {/* <Typography>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse malesuada lacus ex,
            sit amet blandit leo lobortis eget.
          </Typography> */}
        </AccordionDetails>
      </Accordion>   


    </div>  
  )
}


export default Sidebar;